




















































	//引用mSearch组件，如不需要删除即可
	import mSearch from '@/components/search/mehaotian-search-revision.vue';
	import twoGoods from '@/components/lama-goods-vue/two-goods.vue';
	import uniIcon from '@/components/uni-icon.vue';
	import headerSearch from '@/components/common/header-search.vue';
	export default {
		data() {
			return {
				headerPosition: "fixed",
				defaultKeyword: "",
				keyword: "",
				oldKeywordList: [],
				hotKeywordList: [],
				keywordList: [],
				forbid: '',
				isShowKeywordList: true
			}
		},
		onLoad() {
			this.init();
		},
		components: {
			//引用mSearch组件，如不需要删除即可
			mSearch,
			twoGoods,
			headerSearch,
			uniIcon
		},
		methods: {
			navigateBack() {
				uni.navigateBack();
			},
			init() {
				this.loadDefaultKeyword();
				this.loadOldKeyword();
				// this.loadHotKeyword();

			},
			//加载默认搜索关键字
			loadDefaultKeyword() {
				//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
				this.defaultKeyword = "请输入关键字查询";
			},
			//加载历史搜索,自动读取本地Storage
			loadOldKeyword() {
				uni.getStorage({
					key: 'searchOldKey',
					success: (res) => {
						var OldKeys = JSON.parse(res.data);
						this.oldKeywordList = OldKeys;
					}
				});
			},
			//加载热门搜索
// 			loadHotKeyword() {
// 				//定义热门搜索关键字，可以自己实现ajax请求数据再赋值
// 				this.hotKeywordList = ['键盘', '鼠标', '显示器', '电脑主机', '蓝牙音箱', '笔记本电脑', '鼠标垫', 'USB', 'USB3.0'];
// 			},
			//顶置关键字
			setkeyword(data) {
				this.keyword = data.keyword;
			},
			//清除历史搜索
			oldDelete() {
				uni.showModal({
					content: '确定清除历史搜索记录？',
					success: (res) => {
						if (res.confirm) {
							this.oldKeywordList = [];
							uni.removeStorage({
								key: 'searchOldKey'
							});
						} else if (res.cancel) {
						}
					}
				});
			},
			//热门搜索开关
// 			hotToggle() {
// 				this.forbid = this.forbid ? '' : '_forbid';
// 			},
			//执行搜索
			doSearch(key) {
				key = key ? key : this.keyword ? this.keyword : this.defaultKeyword;
				this.keyword = key;
				this.saveKeyword(key); //保存为历史 
				// console.log(this.keyword);
				// 设置弹出框
				uni.showToast({
					title: key,
					icon: 'none',
					duration: 2000
				});
				uni.navigateTo({
					url:'../goods/goods_list?keyword=' + key
				})
				// :url="'/pages/goods/goods_list?keyword='+keyword"
//


			},
			//保存关键字到历史记录
			saveKeyword(keyword) {
				uni.getStorage({
					key: 'searchOldKey',
					success: (res) => {
						// console.log(res.data);
						var OldKeys = JSON.parse(res.data);
						var findIndex = OldKeys.indexOf(keyword);
						if (findIndex == -1) {
							OldKeys.unshift(keyword);
						} else {
							OldKeys.splice(findIndex, 1);
							OldKeys.unshift(keyword);
						}
						//最多10个纪录
						OldKeys.length > 10 && OldKeys.pop();
						uni.setStorage({
							key: 'searchOldKey',
							data: JSON.stringify(OldKeys)
						});
						this.oldKeywordList = OldKeys; //更新历史搜索
					},
					fail: (e) => {
						var OldKeys = [keyword];
						uni.setStorage({
							key: 'searchOldKey',
							data: JSON.stringify(OldKeys)
						});
						this.oldKeywordList = OldKeys; //更新历史搜索
					}
				});
			}
		}
	}
